/******************************
HOMEPAGE WHEN USER IS LOGGED IN
*******************************/
.complete--post--container {
  width: 100%;
  margin-top: 4.6em;
  padding: 3em 2em;
  font-family: inherit;
}
.complete--post--container article {
  max-width: 540px;
  margin: 0 auto 2em;
  background-color: var(--color-white);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
  border-radius: 0.5em;
  display: grid;
  grid-template-columns: 1fr;
}
.complete--post--container article:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.35);
}

/*************************
COMPLETE POST
**************************/
.post {
  /***************************
  POST AUTHOR RELATED STYLING
  ***************************/
  /****************************
  POST BUTTONS RELATED STYLING
  *****************************/
}
.post__header {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding: 1em;
}
.post__author__container {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
}
.post__author__img__wrapper a {
  text-decoration: none;
  display: block;
  margin-right: 0.5em;
  width: 3em;
  height: 3em;
  border-radius: 50%;
}
.post__author__img__wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 50%;
  border: 2px solid var(--general-color);
}
.post__author__name__container div {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
}
.post__author__name__container div a {
  text-decoration: none;
  display: block;
}
.post__author__name__container .author__name {
  font-size: 0.9em;
  color: var(--h1-color);
  line-height: 1.3;
}
.post__author__name__container .author__name:hover, .post__author__name__container .author__name:focus {
  opacity: 0.65;
}
.post__author__name__container .current__user__label {
  display: block;
  margin-left: 1em;
  padding: 1px 8px;
  border-radius: 3px;
  color: var(--color-white);
  font-size: 0.7em;
  background-color: #7b19dd;
}
.post__author__name__container span {
  line-height: 1;
  font-size: 0.85em;
  display: block;
  color: #494c59;
  font-family: "Ubuntu", sans-serif;
}
.post__related__btn__links__container {
  position: relative;
}
.post__related__btn__links__container .sideNavShowBtn,
.post__related__btn__links__container .sideNavHideBtn {
  background: transparent;
  border: none;
  cursor: pointer;
}
.post__related__btn__links__container .sideNavShowBtn i,
.post__related__btn__links__container .sideNavHideBtn i {
  padding: 0.15em;
}
.post__related__btn__links__container div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  top: -1em;
  right: -1em;
  width: 8em;
  padding: 2em 0.75em 1em;
  display: none;
  background-color: var(--color-white);
  border-radius: 0.3em;
  box-shadow: -4px 4px 6px rgba(0, 0, 0, 0.3);
}
.post__related__btn__links__container div a {
  font-size: 0.9em;
  text-decoration: none;
  display: block;
  color: var(--general-color);
}
.post__related__btn__links__container div a i {
  margin-right: 0.2em;
}
.post__related__btn__links__container div a:hover, .post__related__btn__links__container div a:focus {
  opacity: 0.7;
  cursor: pointer;
}
.post__related__btn__links__container div > a {
  margin-bottom: 0.25em;
}
.post__related__btn__links__container div .sideNavHideBtn {
  position: absolute;
  top: 0.75em;
  right: 1em;
}

/***************************
POST ASSETS
***************************/
.post__content__container {
  padding: 0 1em;
}
.post__content__container p {
  color: var(--label-color);
  font-family: "Noto Sans", sans-serif;
  line-height: 1.5;
  font-size: 1em;
  margin-bottom: 0.5em;
}
.post__image__container img {
  width: 100%;
  display: block;
}

/************************
LIKE AND COMMENT BTNS
*************************/
.post__footer {
  border-top: 1px solid var(--input-border-color);
  padding: 0.25em;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
}

.heart__btn,
.comment__btn {
  text-decoration: none;
  display: block;
  color: #3f3d4f;
  border: none;
  background-color: transparent;
  font-size: 1.5em;
}
.heart__btn span,
.comment__btn span {
  font-size: 0.85em;
  font-family: "Noto Sans", sans-serif;
}

.heart__btn {
  cursor: pointer;
}

.heart__btn__active {
  color: var(--grad-color-1);
}

/**************************
COMMENT ACTION STYLING
************************/
.current__user__comment__container {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  border-top: 1px solid #999;
  border-bottom: 1px solid #999;
  padding: 0.75em 1em;
}

.current__user__avatar {
  width: 2.75em;
  height: 2.5em;
  border-radius: 50%;
}
.current__user__avatar img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid var(--general-color);
}

.comment__form {
  width: 100%;
  margin-left: 1em;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}
.comment__form textarea {
  width: 100%;
  resize: none;
  border: none;
  font-family: inherit;
  outline: none;
  font-size: 0.95em;
  height: 70px;
}
.comment__form span {
  font-size: 0.7em;
  letter-spacing: 0.02em;
  color: #ce2727;
}
.comment__form input[type=submit] {
  align-self: flex-end;
  font-family: inherit;
  font-size: 0.85em;
  padding: 0.4em 0.6em;
  font-size: 1em;
  border-radius: 0.5em;
  border: none;
  background-color: var(--btn-color-2);
  color: var(--color-white);
  transition: box-shadow 200ms, transform 200ms;
}
.comment__form input[type=submit]:hover, .comment__form input[type=submit]:focus {
  box-shadow: 7px 7px 10px -2px rgba(0, 0, 0, 0.6);
  transform: translateY(-3px);
  cursor: pointer;
}
.comment__form input[type=submit]:active {
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
  transform: translateY(-2px);
}

.user__comment__wrapper {
  padding: 0.65em 1em;
}

.user__comment__wrapper:not(.user__comment__wrapper:last-child) {
  border-bottom: 1px solid #999;
}

.commented__user__section {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin-bottom: 0.5em;
}

.user__info__section {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
}
.user__info__section a {
  text-decoration: none;
  font-size: 0.85em;
  color: var(--h1-color);
  line-height: 1.3;
  display: block;
}
.user__info__section a:hover, .user__info__section a:focus {
  opacity: 0.65;
}
.user__info__section span {
  line-height: 1.3;
  font-size: 0.85em;
  display: block;
  color: #494c59;
  font-family: "Ubuntu", sans-serif;
}

.user__image__wrapper {
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  margin-right: 0.75em;
}
.user__image__wrapper img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid var(--general-color);
}

.comment__control__btns {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
}
.comment__control__btns a,
.comment__control__btns button {
  color: #666;
}
.comment__control__btns a:hover, .comment__control__btns a:focus,
.comment__control__btns button:hover,
.comment__control__btns button:focus {
  color: #333;
}
.comment__control__btns button {
  border: none;
  background: transparent;
  margin-left: 0.5em;
  cursor: pointer;
}
.comment__control__btns a {
  display: inline-block;
}

.commented__text p {
  font-family: "Noto Sans", sans-serif;
  line-height: 1;
}